class Skills extends React.Component {

	constructor(props) {
		super(props)
	}
	
	render () {
		return (
			<section className="skills">
				<h3>{this.props.title}</h3>
				<ul>
					{this.props.skills.map((skill, i) => {
						return (
							<li key={i}>
								<img src={skill.image} alt={skill.name} />
								<p>{skill.name}</p>
							</li>
						)
					})}
				</ul>
			</section>
		)
	}
}

export default Skills
